export default {
	props:['val','min'],
	data() {
		return {
			 value:this.val
		};
	},
	watch:{
		val(newval){
			this.value=newval;
		}
	},
	methods: {
		minus() {
			this.$emit('minus',parseFloat(this.value)); 
		},
		plus(){
			this.$emit('plus',parseFloat(this.value));
		},
		showkey(){
			this.$emit('showKey')
		}
	}
};
